ne;\n        outline: none;\n\n        &.disabled {\n            pointer-events: none;\n            opacity: 0.5;\n        }\n\n        &:hover {\n            color: $lg-next-prev-hover-color;\n        }\n    }\n\n    .lg-next {\n        right: 20px;\n\n        &:before {\n            content: \"\\e095\";\n        }\n    }\n\n    .lg-prev {\n        left: 20px;\n\n        &:after {\n            content: \"\\e094\";\n        }\n    }\n}\n\n@include keyframes(lg-right-end) {\n    0% {\n        left: 0;\n    }\n\n    50% {\n        left: -30px;\n    }\n\n    100% {\n        left: 0;\n    }\n}\n\n\n@include keyframes(lg-left-end) {\n    0% {\n        left: 0;\n    }\n\n    50% {\n        left: 30px;\n    }\n\n    100% {\n        left: 0;\n    }\n}\n\n\n.lg-outer {\n    &.lg-right-end {\n        .lg-object {\n            @include animation(lg-right-end 0.3s);\n            position: relative;\n        }\n    }\n\n    &.lg-left-end {\n        .lg-object {\n            @include animation(lg-left-end 0.3s);\n            position: relative;\n        }\n    }\n}\n\n// lg toolbar\n.lg-toolbar {\n    z-index: $zindex-toolbar;\n    left: 0;\n    position: absolute;\n    top: 0;\n    width: 100%;\n    background-color: $lg-toolbar-bg;\n\n    .lg-icon {\n        color: $lg-toolbar-icon-color;\n        cursor: pointer;\n        float: right;\n        font-size: 24px;\n        height: 47px;\n        line-height: 27px;\n        padding: 10px 0;\n        text-align: center;\n        width: 50px;\n        text-decoration: none !important;\n        outline: medium none;\n        @include transition(color 0.2s linear);\n\n        &:hover {\n            color: $lg-toolbar-icon-hover-color;\n        }\n    }\n\n    .lg-close {\n        &:after {\n            content: \"\\e070\";\n        }\n    }\n\n    .lg-download {\n        &:after {\n            content: \"\\e0f2\";\n        }\n    }\n}\n\n// lightGallery title\n.lg-sub-html {\n    background-color: $lg-sub-html-bg;\n    bottom: 0;\n   